import React from 'react'
import Socials from './socials'
import wa from '../assets/social/wa.svg'


function Contact() {
    return (
        <section className="contact" id='contact'>
           <h1>Quickly Contact Us</h1>

           <form action="mailto:me@mydomain.com" id='myform'>
               <legend>Email us, it's safe.</legend>
               <div className="left">
               <div className="form-group">
                   <label htmlFor="name">Full Name:</label>
                    <input type="text" name="name" id="name" />
               </div>
               <div className="form-group">
                    <label htmlFor="phone">Phone:</label>
                    <input type="tel" name="phone" id="phone" />
               </div>
               </div>
               <div className="form-group" className='ta'>
                    <label htmlFor="message">Short Message:</label>
                    <textarea name="message" id="message" cols="30" rows="10"></textarea>
               </div>
               <input type="submit" value="Submit" />
               <span className="whatsapp">
                   or submit on <button onClick={()=>sendWhatsapp()}><img src={wa} alt="" /></button>
               </span>
           </form>

            <div className="media" id='media'>
                Or quickly contact us on:
                <Socials />
            </div>

        </section>
    )
}

function sendWhatsapp(){
    function getUser(property){
        return document.getElementById(property).value
    }
    let name = getUser('name')
    let phone = getUser('phone')
    let message = getUser('message')

    let url = `https://wa.me/number?text=My name is ${name} %0a, phone number: ${phone}, and my message is ${message}`

    window.open(url, '_blank').focus()
}


export default Contact
