import React from 'react'
import player from '../assets/footballplayer.png'
import plane1 from '../assets/plane1.png'
import NavMenu from './menu'

function Hero(props) {
    return (
        <div className='hero' id='hero'>
            {(props.displayMenu) ? <NavMenu /> : ''}
            <h1 className='h1'>
                <span className="white">Olawa Sports</span>
                <span className="white">Travelling Agency</span>
            </h1>
            <img src={player} id='p1' alt="Football player" />
            <p className='white'>
                We bring your travel desires, plans and dreams to a reality. Do you want to travel abroad? Do you plan to make a living in another country? Do you want to study abroad? Then we are your best plug for all things travel.
            </p>
            <img src={plane1} id='p2' alt="Plane for background" />
            
        </div>
    )
}

export default Hero
