import React from 'react'
import wa from '../assets/social/wa.svg'
import fb from '../assets/social/fb.svg'
import ig from '../assets/social/ig.svg'
import ms from '../assets/social/ms.svg'

function Socials(){
    return(
        <div className="socials">
            <img src={fb} alt="s" />
            <img src={wa} alt="s" />
            <img src={ig} alt="s" />
            <img src={ms} alt="s" />
        </div>
    )
}

export default Socials
