import React from 'react'

function NavMenu(){
    return (
        <ul className='navMenu white' id='navigate'>
            <li className='active' onClick={()=>navigator('home')}>Home</li>
            <li onClick={()=>navigator('contact')}>Contact Us</li>
            <li onClick={()=>navigator('about')}>About</li>
            <li onClick={()=>navigator('testimonials')}>Testimonials</li>
        </ul>
    )
}

function navigator(e){
    let home = document.getElementById('home')
    let contact = document.getElementById('contact')
    let about = document.getElementById('about')
    let testimonials = document.getElementById('testimonials')

    // let all = ['home', 'contact', 'about', 'testimonials']
    let pages = [home, contact, about, testimonials]
    // let filtered = pages.filter(function(page){
    //     return page.id === e
    // })[0]
    // console.log(filtered)
    
    for (let i of Array(pages.length).keys()){
        if (pages[i].id === e){
            pages[i].scrollIntoView()
            break
        }
    }
}

export default NavMenu