import React from 'react'
import logo from '../assets/logo.png'
import menu from '../assets/menu.svg'
import cancel from '../assets/cancel.svg'
import NavMenu from './menu'

function Nav(props) {
    
    return (
        <nav id='nav'>
            <div className="logo">
                <img src={logo} alt="Olawa logo" />
            </div>
            <div className="menu">
                <img className='butn' src={
                    props.menu ? cancel : menu
                } alt='' onClick={ props.toggleMenu } />
            </div>
            <NavMenu />
        </nav>
    )
}

export default Nav
