import React from 'react'
import plane2 from '../assets/plane2.jpg'
import hand from '../assets/hand.svg'

function About() {
    return (
        <section className="about" id='about'>
            <div className="group">
                <h1 >About Us</h1>

                <div className="about-img">
                    <img id='plane2' src={plane2} alt="" />
                </div>

                <div className='description'>
                    We deal with all things related to travelling. Our sevices cover a wide range. We can bring your plans of going to any country to fulfilment. We put actions into our words. You can trust us. Here are but a few of our travel services:
                </div>

                <div className="service">
                    <Products>Travel for leisure</Products>
                    <Products>Study abroad</Products>
                    <Products>Work abroad</Products>
                    <Products>Change place of permanent residence.</Products>
                    <Products>Special treatment for footballers</Products>
                </div>
            </div>
        </section>
    )
}

function Products(props){
    return(
        <div className="service">
            <span className="hand">
                <img src={hand} alt="" />
            </span>
            <span className="text">
                {props.children}
            </span>
        </div>
    )
}

export default About
