import React from 'react'
import logo from '../assets/logo.png'
import NavMenu from './menu'
import Socials from './socials'

function Footer() {
    return (
        <section id='footer' className='footer'>
            <main>
            <div className="desc">
                <h1>About Us</h1>
                <div className="logo">
                    <img src={logo} alt="logo" />
                </div>
                <p>
                    We bring your travel desires, plans and dreams to a reality. Do you want to travel abroad? Do you plan to make a living in another country? Do you want to study abroad? Then we are your best plug for all things travel.
                </p>
            </div>

            <div className="site-map">
                <h1>Site Map</h1>
                <NavMenu />
            </div>
            </main>

            <div className="contact-us">
                <Socials />
            </div>

            <div className="copyright">
                <span>
                    c Copyright 2021. Olawa Travelling Rights Reserved.
                </span>
            </div>
        </section>
    )
}

export default Footer
