import React, {useState} from 'react';
import './css/app.css';
import { Nav, Hero, About, Contact, Testimonials, Footer } from './components'

function App() {
  const initialState = {
    menu: false,
    value: 'yes'
  }
  const [total, setTotal] = useState(initialState)

  const toggleMenu = () =>{
    let menu
    if (total.menu){
        menu = false
    }else{
        menu = true
    }
    const new_total = {...total}
    new_total.menu = menu
    setTotal(new_total)
  }

  return(
    <React.Fragment>
      <div className="first" id='home'>
        <Nav menu = {total.menu} toggleMenu = {()=>toggleMenu()} />
        <Hero displayMenu = {total.menu}/>
      </div>
      <About />
      <Contact />
      <Testimonials />
      <Footer />
    </React.Fragment>
  )
}

