import React from 'react'

const images = []

for (let i of Array(9).keys()){
    images[i] = [require(`../assets/images/${i+1}.png`), i]  
}

let imgDisplay = images.map((imageGroup)=>
    <img src={imageGroup[0]} alt='Person' key={imageGroup[1]} className='t-image' />
)

function Testimonials() {
    return (
        <section id='testimonials'>
            <h1>Testimonial Images</h1>

            <div className="image-home">
                <div className="images" id='images'>
                    {imgDisplay}
                </div>
            </div>

        </section>
    )
}


export default Testimonials
